@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400&display=swap');

:root {
    --padding: 12.5vh;
    --nav: 50px;
}

* {
    font-family: 'Lato', sans-serif;
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

*::selection {
    background-color: rgb(255, 200, 0);
}

body {
    overflow-x: hidden;
    background-color: #fff;
}

#cursor {
    pointer-events: none;
    position: absolute;
    z-index: 100;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    background-color: #fff;
    mix-blend-mode: exclusion;
    mix-blend-mode: difference;
}


/*! NAV */

nav {
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0 6.5%;
    margin: 0 auto;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 150;
    transition: transform .3s ease-in-out;
}

nav.scrolling-down {
    transform: translateY(-100%);
}

.nav_logo {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.nav_logo img {
    width: 75px;
    height: 75px;
    aspect-ratio: 1/1;
    cursor: pointer;
    user-select: none;
}

.nav_logo p {
    font-size: 1.375rem;
    font-weight: 700;
    margin: 0 1.5rem;
    cursor: pointer;
    letter-spacing: 0.5px;
    word-spacing: 2px;
}

.nav_links span {
    font-size: 1.25rem;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    margin: 0 1.25rem;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 0.2px;
}

.nav_links {
    display: flex;
    justify-content: space-around;
    align-items: center;
}


/*! HEADER */

header {
    background: white;
    clip-path: inset(0 0 4px 0);
}

.header_heading {
    font-family: 'Lato', sans-serif;
    margin: 0;
    text-transform: uppercase;
    text-align: center;
    z-index: 5;
    font-size: 5.5rem;
    line-height: 0.86;
    font-weight: 900;
    letter-spacing: 1rem;
    user-select: none;
}

.header_image {
    margin-top: 17.5vh;
    user-select: none;
    height: 67.5vh;
    width: 100%;
    object-fit: cover;
    object-position: center;
    filter: contrast(0.75);
}


/*! SERVICES */

#services {
    text-align: center;
    width: 80%;
    margin: 10vh auto;
}

#services h1 {
    font-size: 3em;
    font-weight: 900;
    text-transform: uppercase;
}

#services p {
    font-family: 'Roboto';
    font-size: 1.3rem;
}

#services button {
    margin-top: 2.25rem;
    font-family: 'Roboto';
    border: 2px solid #000;
    color: #fff;
    background-color: #000;
    border-radius: 8px;
    outline: none;
    padding: 16px;
    font-size: 1.1rem;
    width: 190px;
    cursor: pointer;
}

#services .contact-button {
    background-color: transparent;
    color: #000;
    margin-right: 1.5rem;
}

.services-examples-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin: 10vh auto;
}

.example-service {
    text-align: left;
    min-width: 380px;
    border-radius: 8px;
    border: 2px solid rgba(0, 0, 0, 0.25);
    overflow: hidden;
    filter: brightness(0.75);
    transition: .2s ease;
}

.example-service:hover {
    filter: brightness(1);
}

.popular-choice {
    font-family: 'Roboto';
    background: rgba(0, 0, 0, 0.25);
    color: #000;
    position: absolute;
    top: 0;
    left: 0;
    padding: 5px;
}

.example-service .service-image {
    position: relative;
    height: 450px;
    width: 100%;
    background-image: url('https://images.unsplash.com/photo-1636589150123-6d57c10527ce?q=80&w=1974&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
}

.example-service .second-example {
    background-image: url('https://images.unsplash.com/photo-1512439408685-2e399291a4e6?q=80&w=1995&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
}

.example-service .third-example {
    background-image: url('https://images.unsplash.com/photo-1598346653523-10a11538b971?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
}

.service-info {
    margin: 0.5rem 0 0.25rem;
    padding: 14px;
}

#services .service-info p {
    font-family: 'Lato';
    font-size: 1.1rem;
}

#services .service-info .service-info-price {
    margin-top: 0.4rem;
    font-size: 1.5rem;
    font-weight: 700;
}


/*! WHY US */

#whyUs {
    width: 80%;
    margin: 17.5vh auto 15vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.whyUs-introduction h1,
.customerReviews-info h1 {
    font-size: 3.25rem;
    font-weight: 900;
    word-spacing: 4px;
    letter-spacing: 0.3px;
}

.whyUs-introduction p,
.customerReviews-info p {
    font-family: 'Roboto', sans-serif;
    font-size: 1.235rem;
    font-weight: 500;
    margin-bottom: 5rem;
}

.whyUs-content-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
}

.whyUs-card {
    padding: 20px;
    border: 2px solid rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin: 1.75vh 0;
    width: 100%;
}

.whyUs-info-icon {
    user-select: none;
    background-image: url('https://images.unsplash.com/photo-1550041473-d296a3a8a18a?q=80&w=2127&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 220px;
    width: 23%;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.225);
}

.whyUs-info-icon.second-icon {
    background-image: url('https://images.unsplash.com/photo-1591453214154-c95db71dbd83?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
}

.whyUs-info-content {
    margin-left: 1.25rem;
    width: 72.5%;
}

.whyUs-info-content h2 {
    font-family: 'Roboto';
    font-weight: 500;
    line-height: 28px;
    font-size: 1.5rem;
}

.whyUs-info-content .technican-description {
    font-family: 'Roboto';
    color: rgba(0, 0, 0, 0.525);
    line-height: 20px;
    font-weight: 400;
    margin: 0.5rem 0;
}

.whyUs-info-content .whyUs-info-description {
    font-family: 'Roboto';
    font-weight: 500;
    line-height: 22px;
    letter-spacing: 0.25px;
    word-spacing: 0.5px;
    font-size: 1.05rem;
    width: 90%;
}

.whyUs-info-characteristic {
    display: inline-block;
    padding: 6px 10px;
    font-family: 'Roboto';
    font-size: 14px;
    background-color: rgba(0, 0, 0, 0.2);
    margin: 0.75rem 0 0.5rem;
    border-radius: 5px;
    user-select: none;
}

.worker-container {
    margin: 0.5rem 0 0;
    display: flex;
    align-items: center;
}

.worker-container .worker-image {
    border-radius: 50%;
    width: 47.5px;
    height: 47.5px;
    object-fit: cover;
    user-select: none;
}

.worker-container .worker-name {
    font-family: 'Roboto';
    font-weight: 500;
    letter-spacing: 0.1px;
    word-spacing: 1px;
    line-height: 20px;
    margin-left: 0.75rem;
}


/*! CUSTOMER REVIEW */

#customerReviews {
    width: 85%;
    margin: 17.5vh auto 15vh;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.customerReviews-info {
    width: 40%;
}

#customerReviews aside {
    width: 60%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.customer_review-card {
    padding: 16px 18px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.155);
    border-radius: 6px;
    min-width: 27.5%;
    max-width: 30%;
    width: auto;
    min-height: 140px;
    height: auto;
}

.customer_review-heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.25rem;
    width: 100%;
}

.customer_review-heading img {
    border-radius: 50%;
    width: 35px;
    height: 35px;
    object-fit: cover;
    object-position: top;
    user-select: none;
}

.customer_review-heading p {
    font-size: 15px;
    overflow: hidden;
    font-family: 'Roboto';
    font-weight: 500;
    line-height: 20px;
    user-select: none;
}

.customer_review-text {
    line-height: 19px;
    font-family: 'Roboto';
    font-weight: 400;
}

.review_stars {
    display: flex;
}

.review_stars .review-star {
    color: #FFB700;
    margin: 0 0.2px;
}


/*! OFFER */

#offer {
    text-align: center;
    margin-bottom: 10vh;
}

#offer h1 {
    font-size: 3.25rem;
    font-weight: 700;
    word-spacing: 4px;
    letter-spacing: 0.3px;
}

#offer>p {
    font-family: 'Roboto';
    font-size: 1.3rem;
}

#offer article {
    width: 82.5%;
    margin: 5rem auto 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
}

.offer_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.offer_container :nth-of-type(3) {
    font-family: 'Roboto';
    font-size: 26px;
    font-weight: 500;
    line-height: 36px;
}

.offer_image {
    background-color: rgba(0, 0, 0, 0.125);
    background-image: url('https://em-content.zobj.net/source/google/313/mobile-phone_1f4f1.png');
    background-position: center;
    background-size: cover;
    border-radius: 50%;
    height: 125px;
    width: 125px;
    margin-bottom: 2rem;
}

.second-offer-image {
    background-image: url('https://em-content.zobj.net/source/twitter/348/battery_1f50b.png');
}

.third-offer-image {
    background-image: url('https://i.pinimg.com/originals/cf/58/f8/cf58f8bb8d9c1638192e11d5b96f020f.png');
}

.offer_name {
    font-family: 'Roboto';
    font-size: 22px;
    font-weight: 400;
    line-height: 28px;
}

.offer_description {
    color: rgba(0, 0, 0, 0.50);
    font-family: 'Roboto';
    font-weight: 400;
    line-height: 24px;
    margin: 0.5rem 0;
}

@layer headerScroll {
    header {
        background-color: white;
    }

    .header_heading {
        position: sticky;
        top: calc(var(--padding) + var(--nav));
        mix-blend-mode: difference;
        color: white;
        transition: mix-blend-mode 0.5s ease;
    }

    .scrolled {
        mix-blend-mode: revert;
    }

    .header_image {
        margin-top: calc(var(--padding) + var(--nav) var(--nav));
    }
}